import SparkMD5 from "spark-md5";

/**
 * 
 * @author zs.duan
 * @date 2023-11-25
 * @param {Object } options 
 * @param {File | string | object | Array} options.file ()  fileId/file/refs 
 * @param {Object} options.Vue vuethis
 * @param {'slice' | 'merge'} options.type   
 * @param {boolean} options.md5 md5  true  false  md5
 * @param {number} options.chunkSize   5M
 * @param {string} options.fileName 
 * @param {boolean} options.sort  true 
 * @param {Function} options.success  
 * @param {Function} options.fail 
 * @returns Promise
 * @description 
 * @returns 
 * { 
 *    code : 200,
 *    list : [
 *      {
            sort : number, //
            file : file, //bold
            filename : string ,// name-1 name-2
            md5 : string //md5
        }
 *    ], //list
 *    filename : string, //
 *    file : file, // blod
 *    md5 : string //md5
 * }
 * @description 
 * @returns
 * {
 *     code : 200,
 *     file : file, // blod
 *     filename : string //
 * }
 * 
 * */
const fileSliceMerge = (options) => {
    let option = {
        file: "", //   fileId/file/refs  
        Vue: null, // vuethis
        type: "slice", //  
        md5: false, // md5  true  false  md5
        chunkSize: 1024 * 1024 * 5, //   5M
        fileName: "", // 
        sort: true, // 
        success: (result) => { }, // 
        fail: (error) => { }, // 
    }
    Object.assign(option, options);
    return new Promise((resolve, reject) => {
        if (!option) {
            reject({ code: -1, msg: "options!" })
            option.fail({ code: -1, msg: "options" })
            throw new Error("options")
        }
        if (typeof options !== "object") {
            reject({code: -1,msg: "options!"})
            option.fail({code: -1,msg: "options"})
            throw new Error("options")
        }
        if (!option.file) {
            reject({code: -1,msg: "options.file!"})
            option.fail({code: -1,msg: "options.file"})
            throw new Error("options.file")
        }
        let files = null;
        if(typeof option.file === "string"){
            if(option.Vue){
                if(!option.Vue.$refs[option.file]){
                    reject({code: -1,msg: "ref!"})
                    option.fail({code: -1,msg: "ref!"})
                    throw new Error("ref!")
                }
                files = option.Vue.$refs[option.file].files;
            }else{
                if(!document.querySelector(`#${option.file}`)){
                    reject({code: -1,msg: "id!"})
                    option.fail({code: -1,msg: "id!"})
                    throw new Error("id!")
                }
                files = document.querySelector(`#${option.file}`).files;
            }
        }else{
            files = option.file;
        }
        switch(option.type){
            case 'slice' : 
                console.log("files ==>" ,files)
                files = files[0] || files
                slice(option, files ,resolve , reject);
                break;
            case 'merge' : 
                if(!files.length){
                    reject({code: -1,msg: "options.file!"})
                    options.fail({code: -1,msg: "options.file!"})
                    throw new Error("options.file!")
                }
                merge(option , files , resolve , reject);
                break;
            default : 
                reject({code: -1,msg: ""})
                options.fail({code: -1,msg: ""})
                throw new Error("")
        }
    })
}

/**
 * 
 * @param {Object} option 
 * @param {File} files 
 * @param {Function} resolve 
 * @param {Function} reject
 * */
const slice = (option , files  , resolve , reject) =>{
    let filelist = [];
    for (let i = 0; i < files.size; i += option.chunkSize) {
        let index = parseInt(i / option.chunkSize) + 1;
        let chunkFile = {
            sort: index,
            file: files.slice(i, option.chunkSize * index),
            filename: `${files.name}-${index}`,
            md5: ""
        }
        if (option.md5) {
            // md5
            let fileReader = new FileReader();
            let spark = new SparkMD5.ArrayBuffer();
            fileReader.readAsArrayBuffer(chunkFile.file);
            fileReader.onload = (e) => {
                spark.append(e.target.result);
                chunkFile.md5 = spark.end();
            }
            fileReader.onerror = (err) => {
                reject({code: -1,msg: "!"})
                options.fail({code: -1,msg: "!"})
                throw new Error("!")
            }
        }
        filelist.push(chunkFile);
    }
    let sendFile = {
        code: 200,
        list: filelist,
        filename: files.name,
        file: files,
        md5: ""
    }
    if(option.md5){
        // md5
        let fileReader = new FileReader();
        let spark = new SparkMD5.ArrayBuffer();
        fileReader.readAsArrayBuffer(files);
        fileReader.onload = (e) => {
            spark.append(e.target.result);
            sendFile.md5 = spark.end();
            option.success(sendFile);
            resolve(sendFile);
        }
        fileReader.onerror = (err) => {
            reject({code: -1,msg: "!"})
            option.fail({code: -1,msg: "!"})
            throw new Error("!")
        }
        return ;
    }
    option.success(sendFile);
    resolve(sendFile);
}

/**
 * 
 * @param {Object} option 
 * @param {Function} resolve 
 * @param {Function} reject
 * */
const merge = (option  , files , resolve , reject) =>{
    if (!files.length) {
        reject({code: -1,msg: ""})
        option.fail({code: -1,msg: ""})
        throw new Error("");
    }

    let filelist = [];
    if (option.sort) {
        // 
        const sortedFiles = Array.from(files).sort((a, b) => {
            const aSuffix = parseInt(a.name.match(/-(\d+)$/)[1]);
            const bSuffix = parseInt(b.name.match(/-(\d+)$/)[1]);
            return aSuffix - bSuffix;
        });
        filelist = sortedFiles.map(file => file.slice(0, file.size));
    } else {
        filelist = Array.from(files).map(file => file.slice(0, file.size));
    }

    const name = option.fileName || files[0].name.replace(/-\d+$/, "");
    const file = new File(filelist, name);

    const sendFile = {
        code: 200,
        file,
        filename: name
    };

    option.success && option.success(sendFile);
    resolve(sendFile);

    return ;
}

export default fileSliceMerge;
